import React from 'react';
import HeaderImg from './logo.jpg';


const Header = () => (
     <header>
     <img className="logo"  src={HeaderImg} alt="Horizon Manufacture and Repair" />
     <div className="contactInfo text-muted " >
               <p className="contact" >Telephone: (801) 936-6945</p>
               <p className="contact">Fax: (801) 936-7177</p>
               <hr />
               <p className="contact">Horizon Manufacturing and Repair Inc.</p>
               <p className="contact">980 W. Center St.</p>
               <p className="contact">North Salt Lake, UT 84054</p>
     </div>

     </header>
);

export default Header;



