import React from 'react';
import { Router, Link } from 'react-static';
import { hot } from 'react-hot-loader';
//
import Routes from '../dist/react-static-routes';
import Header from './components/header';
import 'bootstrap/dist/css/bootstrap.min.css';
import './app.css';

const App = () => (
  <Router>
    <div>
      <Header />
      <nav>
        <div className="headNav ">
          <button type="button" class=" btn btn-rd-left  btn-secondary"><Link to="/">Home</Link></button>
          <button type="button" class=" navBtn btn   btn-secondary"><Link to="/projects">Projects</Link></button>
          <button type="button" class=" btn btn-rd btn-rd-right  btn-secondary"><Link to="/contact">Contact</Link></button>
        </div>
      </nav>
      <div className="content">
        <Routes />
      </div>
    </div>
  </Router>
);

export default hot(module)(App)



