import React from 'react';
import { Router, Link } from 'react-static';
import Fab from './fab-shop.jpg';
import Hyd from './hyd-shop.jpg';
import Mach from './mach-shop.jpg';


const DetailBox = () => (
     <Router>
          <div className="container" >
          <div className="row">
               <div className="detailBox col-sm-4">
               <h3>Fabrication</h3>
                    <img src={Fab} alt="Fabrication" />
                         <button className="btn-primary btn-lg " ><Link to='/projects' /> More</button>
               </div>
                    <div className="detailBox col-sm-4 centerDetail ">
               <h3>Hydraulic Cylinders</h3>
                    <img src={Hyd} />
                    <button className="btn-primary btn-lg  " ><Link to='/contact' /> More</button>
               </div>
               <div className="detailBox col-sm-4">
                    <h3>Machine Shop</h3>
                    <img src={Mach} />
                         <button className="btn-primary btn-lg " > <Link to='/contact' />More</button>
               </div>
               </div>
          </div>
     </Router>
);

export default DetailBox;



